import uniIcons from "../uni-icons/uni-icons.vue";
export default {
	name: "UniSearchBar",
	components: {
		uniIcons
	},
	props: {
		placeholder: {
			type: String,
			default: "请输入搜索内容"
		},
		radius: {
			type: [Number, String],
			default: 5
		},
		clearButton: {
			type: String,
			default: "auto"
		},
		cancelButton: {
			type: String,
			default: "auto"
		},
		cancelText: {
			type: String,
			default: '取消'
		},
		bgColor: {
			type: String,
			default: "#F8F8F8"
		},
		maxlength: {
			type: [Number, String],
			default: 100
		},
		value: {
			type: String,
		}
	},
	data() {
		return {
			show: this.value?true:false,
			focus: false
		}
	},
	methods: {
		searchClick() {
			if (this.show) {
				return
			}
			this.show = true;
			this.focus = true;
		},
		clear() {
			this.$emit("input", '')
			setTimeout(()=>{
				this.show = false
			}, 0)
		},
		blur(){
			this.focus = false
			if(!this.value) this.show = false
		}
	}
};
